import React, { useState, useRef, useEffect} from 'react';
import ReactFileReader from 'react-file-reader';
import './App.css';
import './AppMobile.css';
import { CSVLink } from "react-csv";
import { Button } from '@material-ui/core';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import TextField from '@material-ui/core/TextField';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';  
import {Helmet} from 'react-helmet'
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    listStyle: 'none',
    padding: theme.spacing(0.5),
    margin: 0,
  },
  chip: {
    margin: theme.spacing(0.5),
  },
}));

function App() {
  let [file, setFile] = useState('')
  let [cmWidth,setCmWidth] = useState(64)
  let [displayRatio,setDisplayRatio] = useState(1)
  const canvasRef = useRef(null)
  let [parentCanvasWidth,setParentCanvasWidth] = useState(800)
  let [done,setDone] = useState(true)
  const canvasParentRef = useRef(null);
  const firstUpdate = useRef(true);
  let [csvData,setcsvData] = useState([])
  let [hexInput,setHexInput] = useState('#')
  const classes = useStyles();
  const [chipData, setChipData] = React.useState([
    {key:'e', label:'#EEEEEE'},
    {key:'c', label:'#CCCCCC'},
    {key:'9', label:'#999999'},
    {key:'6', label:'#666666'},
    {key:'3', label:'#333333'},
    {key:'0', label:'#000000'},
  ]); 
  //removes a chip if it is deleted by the user
  const handleDelete = (chipToDelete) => () => {
    setChipData((chips) => chips.filter((chip) => chip.key !== chipToDelete.key));
  };
  //tracks what is being typed in the hex field
  const onHexInputChange = (e) => {
    setHexInput(e.target.value)
  }
  //onclick function for the add button to add the hex code to the chip array
  const hexPush = () => {
    if(/^#([0-9A-F]{3}){1,2}$/i.test(hexInput)){
      let uid = Math.floor((Math.random())*100000000)
      setChipData([...chipData, {key: uid, label: hexInput}])
    }
  }
  //handles the dice in the input field and updates cmwidth,which then calls the dicemaker function
  const onChange = (e) => {
    let numberOfDice
    //checks input is an integer
    let input = parseInt(e.target.value)
    //too many dice alert
    if(input>500) {alert('Please enter a number less than 500')}
    //if input is an integer and less than 200
    input && e.target.value< 500 ? numberOfDice = input : numberOfDice = 1
    setCmWidth(parseFloat((numberOfDice*1.6).toFixed(1)))
  }
  //loads the files initially and stores src
  const handleFiles = files => {
    const img = new Image()
    img.src = files.base64
    setFile(img.src) 
  }
  //monitors changes in any of the variables in the array and calls the dice mosiac maker
  useEffect(() => {
    if(firstUpdate.current) {
      //ensures that create preview isn't called during the first loading of the components when there is no image file yet
      firstUpdate.current = false;
      } 
    else {
      //adjust canvas size to based on screen width (smaller for mobile)
      setParentCanvasWidth(canvasParentRef.current ? canvasParentRef.current.offsetWidth : 0)
      //if there's at least two colors in chip array
      if(chipData.length >= 2){
        //start the 'tiling...' message
        setDone(false)
        //creates new image object
        const img = new Image()
        //sets source of new image object
        img.src = file
        //needed otherwise image hasnt loaded
        img.onload = () => {
            //creating the canvas for photomosaic
            const canvas = canvasRef.current
            const context = canvas.getContext('2d')
            let imgWidth = img.naturalWidth
            let imgHeight = img.naturalHeight
            //ratio needed to calculate length of the mosiac
            let ratio = imgHeight/imgWidth
            //needed to update the text displayed for length in return statement
            setDisplayRatio(ratio)
            //dice are 1.6 cm wide, cmWidth is the default number used to calc mosiac size in cms
            let numDiceWidth = Math.ceil(cmWidth / 1.6)
            let numDiceHeight = Math.ceil(ratio * cmWidth / 1.6)
            //needed to calculate the size of the dice images in context of the canvas width
            console.log(parentCanvasWidth)
            let size = Math.ceil(parentCanvasWidth / numDiceWidth)
            //sets canvas size to expected dimensions of mosiac
            canvas.width = size * numDiceWidth
            canvas.height = size * numDiceHeight
            //reduce image to size where 1 pixel = 1 die
            context.drawImage(img,0,0,numDiceWidth,numDiceHeight);
            //place reduced image into array for the for loop
            let imgPixels = context.getImageData(0, 0, numDiceWidth, numDiceHeight);
            //clears canvas of the drawn image
            context.clearRect(0, 0, numDiceWidth, numDiceHeight);
            //makes it so you dont need to type impixels.data in the for loop
            let data = imgPixels.data
            let indexRows = []
            for(let y = 0; y < imgPixels.height; y++){
              let indexColumns = []
              for(let x = 0; x < imgPixels.width; x++){
              //find current row by calc-ing the length and the current count
              let currentRow = (4 * imgPixels.width) * y 
              // find current index within the row
              let currentPixelInRow = (x * 4)
              // sum both in order to find your index in the current row
              let i = currentRow + currentPixelInRow
              //getting the RGB
              let r = data[i]
              let g = data[i+1]
              let b = data[i+2]
              //convert RGB to hex
              let hex = '#' + [r, g, b].map(x => {
                const hex = x.toString(16)
                return hex.length === 1 ? '0' + hex : hex
              }).join('')
              //create array of tile colors
              let fillColors = chipData.map(colorObj => colorObj.label);
              //set up function to find nearest color from provided tile array
              let nearestColor = require('nearest-color').from(fillColors);
              //calculates postion of dice based on x and y in for loop
              let xnew = x * size
              let ynew = y * size
              //draws dice image at the desired locations
              context.fillStyle = nearestColor(hex) 
              context.fillRect(xnew,ynew, xnew+size, ynew + size)
              context.strokeStyle = "#999999"
              context.lineWidth = 1
              context.strokeRect(xnew,ynew, xnew+size, ynew + size)
              //adds the hex to the csv row
              indexColumns.push(nearestColor(hex))
            }
            //adds row of hex values to the csvData array (each row is a row in a csv)
            indexRows.push(indexColumns)
          }
          //this deactivates the 'tiling...' message after the for loop is done
          setDone(true)
          //this sets the csvData variable to the data created from the most recent tiling
          setcsvData(indexRows)
        }
      }
      else{
        //alert if someone hasn't added two colors, otherwise canvas is just one color
        alert('Please add at least 2 colors')
      }
    }
  }, [file,chipData,cmWidth,parentCanvasWidth])

    return (
      <div className='grid-container'>
        <Helmet>
        <title>Mozaic: A Mosiac Generator to Create and Export Tile Mosiacs</title>
        <meta name="description" content="Upload photos and easily convert them into tiled mosiacs. Choose colors to represent your tiles. Adjust the number of tiles in your mosiac. Export your design to a CSV file." />
        </Helmet>
  
      <div className='uploadArea'>
        <div className="logo">
              <Typography className='logoTitle' gutterBottom variant="h3" component="h3" color='primary'>
                Mozaic |
              </Typography>
              <Typography className='logoDescription' gutterBottom variant="h5" component="h5" color='primary'>
                Create, Export Tile Mosiacs
              </Typography>
        </div>
        <div className='upload'>
          <ReactFileReader handleFiles={handleFiles} base64={true}>
              <Button
              variant="contained"
              color="primary"
              startIcon={<CloudUploadIcon />}
            >
              Upload
          </Button>
          </ReactFileReader>
        </div>
        <div className='inputTile'>
          <TextField
          id="outlined-number"
          label="Tiles"
          type="number"
          defaultValue='40'
          InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
          onChange={(event) => onChange(event)}
           />
        </div>
        <div className='hexButton'>
            <Button
              variant="contained"
              color="primary"
              onClick= {hexPush}
            >
              Add
            </Button>
        </div>
        <div className='hex'>
            <Paper id='hexChild' component="ul" className={classes.root} >
            {chipData.map((data) => {
              
              return (
                <li key={data.key}>
                  <Chip
                    icon={<input className = 'color' type="color" disabled value={data.label}></input>}
                    label={data.label}
                    onDelete={handleDelete(data)}
                    className={classes.chip}
                  />
                </li>
              );
            })}
            </Paper>
        </div>
      <div className='inputColor'>
        <TextField id="hex" label="Hex" name="hex"
          value={hexInput}
          onChange={(event) =>  onHexInputChange(event)}
          error={hexInput!=='#' && !/^#([0-9A-F]{3}){1,2}$/i.test(hexInput)}
          helperText={hexInput!=='#' && !/^#([0-9A-F]{3}){1,2}$/i.test(hexInput) ? 'Invalid hex.' : ' '}
          variant="outlined"
        />
      </div>
      </div>
      <div className='widthInfoArea'>
      <div className='widthInfo'>
        <Typography variant="body1" color="textSecondary" component="p">
           {Math.ceil(cmWidth / 1.6)} tiles
        </Typography>
      </div>
      {done === false && file ?  
              <Typography className='loadingInfo' variant="body1" color="textSecondary" component="p">
                 Tiling...
              </Typography> : <div></div>}
      </div>
      <div className='lengthInfo'>
        <div className='length'>
        <Typography variant="body1" id="rotate-text" color="textSecondary" component="p">
          {Math.ceil(displayRatio * cmWidth / 1.6)} tiles
        </Typography>
        </div>
      </div>

      <div ref={canvasParentRef} className ='imgParent'>
        <Card >
          <CardActionArea >
          <canvas ref={canvasRef}/> 
            <CardContent>
              <Typography variant="h6" color="textSecondary" component="h6">
                {Math.ceil(cmWidth / 1.6)*Math.ceil(displayRatio * cmWidth / 1.6)} tiles
              </Typography>
              {csvData.length>0 ? chipData.map(chip => {

               return <div className='tileCount'>
                      <input className = 'color' type="color" disabled value={chip.label}></input>
                      <Typography variant="p" color="textSecondary" component="p">
                        {chip.label}, {csvData.flat().filter(x => x === chip.label).length} tiles 
                      </Typography>
                      </div>
              })
               : <Typography variant="body1" color="textSecondary" component="p">
                  Upload a photo, adjust number of tiles, remove grays, add desired hex codes.
                 </Typography>}
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size="small" color="primary">
              <CSVLink className='link' data={csvData} filename={"mosiac.csv"}>Export</CSVLink>
            </Button>
            <Button size="small" color="primary" href="https://amzn.to/32vk1WF" target='_blank'>
              Tiles on Amazon
            </Button>
          </CardActions>
        </Card>
        <Typography variant="body2" color="textSecondary" className='affiliate' component="p">
          Disclosure: As an Amazon Associate, I would earn from qualifying purchases.
        </Typography>
      </div>
    </div>
    );
}

export default App;
